import classNames from "classnames";
import { Dispatch, SetStateAction, useRef } from "react";
import { useEffect } from "react";
import { Pages } from "../../App";
import "./SidebarPanel.css";

export const SidebarPanel = (props: { setSelectedPage: (page: Pages) => void, isOpen: boolean, selectedPage: Pages, setOpen: Dispatch<SetStateAction<boolean>> }) => {
    let sidebarPanelElement = useRef<HTMLDivElement>(null);
    
    const handleClickOutside = (event: MouseEvent) => {
        const openButton = document.querySelector(".frame-header__icon");
        if (!sidebarPanelElement.current?.classList.contains("hide") && event.target !== openButton &&
            !sidebarPanelElement.current?.contains(event.target as HTMLElement) && sidebarPanelElement.current !== event.target) {
            props.setOpen(false);
        }
    }

    useEffect(() => {
        document.addEventListener('mousedown', handleClickOutside);
        return () => document.removeEventListener('mousedown', handleClickOutside);
    }, [])

    return (
        <div className={props.isOpen ? "sidebar-panel" : "sidebar-panel  hide"} ref={sidebarPanelElement}>
            <button className={classNames("sidebar-panel__button", props.selectedPage === Pages.TodoList && "select")} onClick={() => props.setSelectedPage(Pages.TodoList)}>ToDo List</button>
            {/* <button className={classNames("sidebar-panel__button", props.selectedPage === Pages.Table && "select")} onClick={() => props.setSelectedPage(Pages.Table)}>Custom desk</button> */}
            <button className={classNames("sidebar-panel__button", props.selectedPage === Pages.Week && "select")} onClick={() => props.setSelectedPage(Pages.Week)}>Week</button>
            {/* <button className={classNames("sidebar-panel__button", props.selectedPage === Pages.Today && "select")} onClick={() => props.setSelectedPage(Pages.Today)}>Today</button> */}
            <button className={classNames("sidebar-panel__button", props.selectedPage === Pages.Calendar && "select")} onClick={() => props.setSelectedPage(Pages.Calendar)}>Calendar</button>
        </div>
    )
}