import classNames from "classnames";
import { useState } from "react";
import { Todo } from "../../reducers/newSlice";
import { Modal } from "../Modal/Modal";
import "./Menu.css";

export interface IMenuProps {
    editItem: () => void;
    deleteItem: (task: Todo) => void;
    task: Todo;
}

export const Menu = (props: IMenuProps) => {
    const {editItem, deleteItem, task} = props;
    const [open, setOpen] = useState<boolean>(false)
    const [coordinates, setCoordinates] = useState<{x: number,y: number}>();
    const onClick = (e: React.MouseEvent) => {
        e.stopPropagation();
        e.preventDefault();
        setCoordinates({x: e.clientX - 180, y: e.clientY})
        setOpen(true);
    }

    const onItemClick = (e: React.MouseEvent, handler: Function) => {
        e.stopPropagation();
        handler();
        setOpen(false);
    }
    return (
        <>
            <div className="menu__button" onClick={onClick}></div> 
            <Modal key="menu" open={open} setOpen={setOpen} onCancel={() => setOpen(false)} coordinates={coordinates} className={"menu__modal"}>
                <div className="menu__items">
                    <div className="menu__item" onClick={(e: React.MouseEvent) => onItemClick(e, editItem)}>
                        <div className="item-list_edit-button"></div>
                        <div></div>
                    </div>
                    <div className="menu__item" onClick={(e: React.MouseEvent) => onItemClick(e,()=>deleteItem(task))}>
                        <div className="item-list_delete-button"></div>
                        <div></div>
                    </div>
                </div>
            </Modal>  
        </>
        
    )
}