import classNames from "classnames";
import { Todo } from "../../reducers/newSlice";
import { Menu } from "../Menu/Menu";
import "./ItemList.css"

export interface IItemListProps {
    task: Todo;
    deleteItem: (task: Todo) => void;
    changeDone: (task:Todo) => void;
    onDragStartHandler: (e: React.DragEvent<HTMLDivElement>, task:Todo) => void;
    onDropHandler: (e: React.DragEvent<HTMLDivElement>, task:Todo) => void;
    editItem: () => void;
}

export const ItemList = (props: IItemListProps) => {
    const { task, changeDone, editItem } = props;

    const onDragOverHandler = (e: React.DragEvent) => {
        e.preventDefault();
    }

    
    return (
    <div  className={task.done ? "item-list done" : "item-list"}  onClick={editItem} draggable={true}
        onDragStart={(e) => props.onDragStartHandler(e, task)} onDrop={(e) => props.onDropHandler(e, task)} onDragOver={onDragOverHandler }>
            <div className="item-list__title">
                <button onClick={(e) => {e.stopPropagation(); changeDone(task)}} className={classNames(task.done ? "item-list_done-button" : "item-list_circle-button")}></button>
                <div className="item-list_name">{task.title}</div>
            </div>
            <Menu editItem={editItem} deleteItem={props.deleteItem} task={task}/>
        </div>
    )
}