import classNames from "classnames";
import { Dispatch, SetStateAction } from "react";
import "./Modal.css";

export interface IModalProps {
    open: boolean;
    setOpen: Dispatch<SetStateAction<boolean>>;
    header?: React.ReactNode;
    children?: React.ReactNode;
    buttons?: React.ReactNode[];
    onCancel?: () => void;
    coordinates?: {x: number,y: number};
    className?: string;
}


export const Modal = (props: IModalProps) => {

    const onModalClick = (e: React.MouseEvent) => {
        e.stopPropagation();
        props.onCancel && props.onCancel();
    }
    
    return <div className={classNames("modal", props.open && "active", props.className)} onClick={onModalClick}>
        <div className="modal__wrapper" onClick={(e)=> e.stopPropagation()} style={{left: props.coordinates?.x, top: props.coordinates?.y}}>
            <div className="modal__header"></div>
            <div className="modal__content">{props.children}</div>
            <div className="modal__buttons">{props.buttons}</div>
        </div>
    </div>
}