import React from "react";
import { Dispatch, SetStateAction, useEffect } from "react";
import { Button } from "../Button/Button";
import { Modal } from "../Modal/Modal";
import "./AddTaskModal.css";
import TextareaAutosize from 'react-textarea-autosize';

export interface IAddTaskModalProps {
    open: boolean;
    setOpen: Dispatch<SetStateAction<boolean>>;
    onCancel: () => void;
    addItem: () => void;
    inputValue: string;
    setInputValue: Dispatch<SetStateAction<string>>;
}

export const AddTaskModal = (props: IAddTaskModalProps) => {
    const {open, setOpen, onCancel, addItem, inputValue, setInputValue} = props;
    const buttons = [
        <Button text={""} onClick={onCancel}></Button>,
        <Button text={""} className={"add-task-button"}  onClick={addItem}></Button>
      ]
    
    return (
        <Modal key="add-task" open={open} setOpen={setOpen} buttons={buttons} onCancel={onCancel}>
            <div className={"modal-content"}>
                <div>{"  "}</div>
                <TextareaAutosize className={"add-task-modal__input-field"} onChange={(e) => setInputValue(e.target.value)} value={inputValue} minRows={2}></TextareaAutosize>
            </div>
        </Modal>)
}