import "./ColumnItem.css";

export interface IColumnItemProps {
    name: string;
    onClick: (el:string) => void;
}

export const ColumnItem = (props: IColumnItemProps) => {
    return (
    <div className="column-item" draggable={true}>
        {props.name}
        <button onClick={()=>props.onClick(props.name)} className="column-item__button"></button>
    </div>
    )
    
}